<template>
    <v-pagination color="#333" v-model="pagination.currentPage" :length="pagination.totalPages" :total-visible="7"
        @input="onPageChange"></v-pagination>

</template>

<script>
import axios from 'axios'
import {eventBus} from '../main'
export default {
    mame: 'pagination',
    data() {
        return {
            pagination: {
                currentPage: 1,
                totalPages: 0
            }
        }
    },
    created() {
        eventBus.$on('totalPages', (page) => {
            this.pagination.totalPages = page;
        }),
            eventBus.$on('currentPage', (page) => {
                this.pagination.currentPage = page;
            })
    },

    methods: {
        onPageChange() {
            eventBus.$on('totalPages', (page) => {
                this.totalPages = page;
            }),
                eventBus.$on('currentPage', (page) => {
                    this.currentPage = page;
                })
            eventBus.$emit('changedPage', this.pagination.currentPage)
        }
    }
}
</script>

