<template>

    <v-row class="mt-n14">

        <v-col cols="12" xs="12" sm="6" md="4" lg="3" v-for="product in products.content" :key="product.id">

            <v-card class="mx-auto my-12 rounded-xl" max-width="374" color="#151515">

                <v-img class="ImagemProduto" @click="productLink(product.id)" height="170" :src="product.image">
                </v-img>

                <v-toolbar color="transparent" class="mt-n7" flat>
                    <v-avatar color="white" rounded class="mr-2">
                        <v-img src="https://staticm.kabum.com.br/public/favicon.ico" contain></v-img>
                    </v-avatar>

                </v-toolbar>
                <v-card-title style="overflow-y: auto; height:150px"
                    class="grey--text text-grey-darken-1 caption mt-n6">{{ product.name }}
                </v-card-title>
                <v-card-title class="white--text caption justify-center"> R$
                    {{ formatPrice(product.price) }}
                </v-card-title>
                <v-btn block color="grey" elevation="12">Monitorar</v-btn>

            </v-card>

        </v-col>

    </v-row>

</template>

<script>
import axios from "axios";
import { eventBus } from "../main";
export default {
    mame: 'Products',
    props: ['page'],
    data() {
        return {
            products: [],
            searchTerm: '',
            currentPage: 0,
            totalPages: 0,
        }
    },
    watch: {
        currentPage: function (page) {
            page.getProducts(this.searchTerm);

        }
    },
    beforeMount() {
        this.getProducts();
    },
    mounted() {
        eventBus.$on('changedPage', (page) => {
            this.currentPage = page;
            this.getProducts(this.searchTerm);
        });
        this.$root.$on('searchEvent', (search) => {
            this.searchTerm = search;
            this.currentPage = 0;
            this.getProducts(this.searchTerm);
        })

    },
    methods: {
        async getProducts(searchTerm) {

            let config = {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Access-Control-Allow-Headers": "Authorization",
                    "Access-Control-Allow-Methods": "GET, POST, OPTIONS, PUT, PATCH, DELETE",
                    "Content-Type": "application/json;charset=UTF-8"
                }

            }

            const response = await axios.get('https://compre-barato-api.herokuapp.com/products/', {
                params: {
                    'searchTerm': searchTerm ?? '',
                    'page': this.currentPage ?? 0,
                    'size': '28',
                    'sort': 'price,desc'

                }

            })

            this.products = response.data;
            this.currentPage = response.data.number;
            this.totalPages = response.data.totalPages - 1;
            eventBus.$emit('totalPages', this.totalPages);
            eventBus.$emit('currentPage', this.currentPage);
        },
        productLink(id) {
            window.open("https://www.kabum.com.br/produto/" + id, '_blank').focus();
        },
        formatPrice(price) {
            const val = Number(price.toString().replace(",", "."));
            if (!val) return '0,00';
            const valueString = val.toFixed(2).replace(".", ",");
            return valueString.replace(/\B(?=(\d{3})+(?!\d))/g, ".");

        }
    }
}
</script>

<style scoped>
.white--text {
    font-size: 20px !important;
    text-align: center !important;

}


.ImagemProduto {
    cursor: pointer;
}
</style>

